<template>
  <div class="api_list">
    <div class="api_list_add">
      <el-button type="primary" icon="edit" @click="editClick(-1)"></el-button>
      <el-dialog v-model="dialogFormVisible" :modal-append-to-body="false" :show-close="false" @open="dialogOpen"
                 @close="dialogClose">
        <reqEditor :dialog_id="dialog_id" ref="editor"></reqEditor>
      </el-dialog>
    </div>
    <el-table :data="tableData" v-loading="loading" element-loading-text="" border>
      <el-table-column prop="date" label="" sortable width="200"></el-table-column>
      <el-table-column prop="update" label="" sortable width="200"></el-table-column>
      <el-table-column prop="name" sortable label="API">
      </el-table-column>
      <el-table-column prop="status" label="" width="100"
                       :filters="[{ text: '', value: true }, { text: '', value: false }]"
                       :filter-method="filterTag">
        <template scope="scope">
          <el-switch
            v-model="scope.row.status"
            on-text=""
            off-text=""
            @change="changeStatus(scope.row._id,scope.row.status)">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="100">
        <template scope="scope">
          <el-button @click="deleteClick(scope.row._id)" type="text" size="small"></el-button>
          <el-button @click="editClick(scope.row._id)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  import reqEditor from '../editor/reqEditor.vue'
  import {_getReqApiList, _delReqApi, _setApiStatus} from '../../javascript/getData'
  import moment from 'moment'
  export default{
    components: {
      reqEditor
    },
    data() {
      return {
        loading: false,
        dialogFormVisible: false,
        dialog_id: -1,
        tableData: []
      }
    },
    methods: {
      /**
       * Api
       */
      changeStatus(id, status){
        _setApiStatus({id, status, list: "req"}).then((res) => {
          const data = res.data;
          if (data.code === 200) {
            this.$message.success({
              message: ""
            });
          } else {
            this.$message.error({
              message: data.msg
            });
          }
        }).catch((err) => {
          this.$message.error({
            message: ""
          });
        })
      },
      /**
       * 
       * editor
       */
      dialogOpen(){
        this.$nextTick(function () {
          this.$refs['editor'].$emit('openDialog');
          this.$refs['editor'].$on('hideDialog', (res) => {
            if (res.listchange) {
              this.getListInfo();
            }
            this.dialogFormVisible = false;
          });
        })
      },
      dialogClose(){
        this.$refs['editor'].$emit('closeDialog');
      },
      /**
       * Api
       * @param pid
       */
      deleteClick(pid){
        this.$confirm('API, ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'info'
        }).then(() => {
          _delReqApi({id: pid}).then((res) => {
            const data = res.data;
            if (data.code === 200) {
              this.tableData = _.filter(this.tableData, (item) => item._id !== pid);
              this.$message.success({
                message: "Api"
              });
            } else {
              this.$message.error({
                message: data.msg
              });
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      /**
       * /Api
       * @param pid
       */
      editClick(pid) {
        this.dialog_id = pid;
        this.dialogFormVisible = true;
      },
      /**
       * Element
       * @param value
       * @param row
       * @returns {boolean}
       */
      filterTag(value, row) {
        return row.status === value;
      },
      /**
       * 
       */
      getListInfo(){
        this.loading = true;
        _getReqApiList().then((res) => {
          this.loading = false;
          const data = res.data, result = data.result;
          if (data.code === 200) {
            moment.locale('zh-cn');
            this.tableData = _.forEach(result, (item) => {
              item.date = moment(item.date).format('lll');
              item.update = moment(item.update).format('lll');
            });
          } else {
            this.$message.error({
              title: '',
              message: data.msg
            });
          }
        });
      },
    },
    mounted(){
      /**
       * 
       */
      this.getListInfo();
    }
  }
</script>



