<template>
  <div class="reqEditor">
    <div slot="title" class="dialogForm_title">
      <span class="el-dialog__title" v-if="dialog_id === -1 ">API</span>
      <span class="el-dialog__title" v-else>API</span>
    </div>
    <div class="dialogForm_body">
      <el-form :model="form" label-width="120px">
        <el-form-item label="API">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-switch on-text="" off-text="" v-model="form.status" :width="60"></el-switch>
        </el-form-item>
        <div style="position: relative">
          <div style="margin-bottom: 20px;">
            <el-popover ref="popover1" placement="bottom" width="160" v-model="addFormVisible">
              <p></p>
              <el-input v-model="addName" size="mini" auto-complete="off" style="margin-bottom: 5px;"></el-input>
              <div style="text-align: right; margin: 0">
                <el-button size="mini" type="text" @click="addFormReset"></el-button>
                <el-button type="primary" size="mini" @click="addFormSubmit"></el-button>
              </div>
            </el-popover>
            <el-button v-popover:popover1 type="text" style="position: absolute;right: 18px;bottom: 80px;z-index: 1">
              
            </el-button>
          </div>
          <el-tabs v-model="form.template" type="card" closable @tab-remove="removeTab">
            <el-tab-pane v-for="item in templateOptions" :label="item.label" :name="item.value"></el-tab-pane>
          </el-tabs>
        </div>
        <el-form-item label="">
          <el-select v-model="form.type" placeholder="" >
            <el-option
              v-for="item in options"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
            <el-button type="text"  @click="addFromItem" class="add_Btn" v-if="form.type===2"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="json_editor_wrapper" v-show="form.type === 1">
      <div class="json_editor" ref="json_editor"></div>
    </div>
    <div v-show="form.type ===2" class="reqData_body reqData_body_1">
      <template v-for="(item,index) in formData">
        <div class="reqData_item">
          <el-col :span="10">
            <el-input v-model="item.key" placeholder="key"></el-input>
          </el-col>
          <el-col :span="10">
            <el-input v-model="item.value" placeholder="value"></el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" icon="delete" plain @click="deleteFormItem(index)"></el-button>
          </el-col>
        </div>
      </template>
    </div>
    <div v-show="form.type ===3" class="reqData_body reqData_body_2">
      <el-input
        type="textarea"
        autosize
        placeholder=""
        :autosize="{ minRows: 2, maxRows: 4}"
        v-model="raw">
      </el-input>
    </div>
    <div slot="footer" class="dialog-footer json_editor_group">
      <el-button @click="reset()"> </el-button>
      <el-button type="primary" @click="submitEditor()"> </el-button>
    </div>
  </div>
</template>
<script>
  import JSONEditor from 'jsoneditor';
  import {_getReqApi, _addReqApi} from '../../javascript/getData'
  export default{
    name: 'jsoneditor',
    props: ['dialog_id'],
    data(){
      return {
        //
        options: [{
          value: 1,
          label: "application/json"
        }, {
          value: 2,
          label: "application/x-www-form-urlencoded"
        }, {
          value: 3,
          label: "raw"
        }],
        reqArr: {}, //
        formData: [  //FormData 
          {
            key: "",
            value: "",
          }
        ],
        //
        form: {
          name: '',
          status: false,
          type: 1,
          template: "1"
        },
        raw: "",
        templateOptions: [],
        addFormVisible: false, //Tabpopover
        addName: '' //Tab
      }
    },
    methods: {
      /**
       * Tab
       */
      addFormReset(){
        this.addName = '';
        this.addFormVisible = false;
      },
      /**
       * Tab
       */
      addFormSubmit(){
        const index = _.maxBy(this.templateOptions, (o) => +o.value).value;
        //Tab
        const newTab = {
          value: (+index + 1).toString(),
          label: this.addName
        };
        const newContent = {
          type: 1,
          reqData: {
            code: 200,
            message: "",
            result: ""
          }
        };
        this.templateOptions.push(newTab);
        this.reqArr[+index + 1] = newContent;
        this.addFormReset();
      },
      /**
       * Tab
       */
      removeTab(name){
        if (this.templateOptions.length <= 1) {
          this.$message({
            showClose: true,
            message: 'Tab',
            type: 'warning'
          });
          return;
        }
        //
        _.forEach(this.templateOptions, (option, index) => {
          if (option.value === name) {
            this.templateOptions.splice(index, 1);
            delete this.reqArr[index + 1];
            //TabTab
            if (this.form.template === name) {
              this.form.template = this.templateOptions[0].value;
            }
            return !1;
          }
        });
      },
      /**
       * watch
       */
      setWatch(){
        this.unwatch = this.$watch('form.template', function (newTpl, oldTpl) {
          const Otype = this.form.type;
          const Ntype = this.reqArr[newTpl].type;
          var Oitem;
          //
          switch (Otype) {
            case 1:
              try {
                Oitem = this.editor.get();
              } catch (err) {
                this.$notify.error({
                  title: "",
                  message: err.toString()
                });
                return;
              }
              break;
            case 2:
              Oitem = this.formData;
              break;
            case 3:
              Oitem = this.raw;
              break;
            default:
              break;
          }
          this.reqArr[oldTpl] = {
            type: this.form.type,
            reqData: Oitem
          };

          //
          this.editor.set({request: ""});
          this.raw = '';
          this.formData = [
            {
              key: "",
              value: "",
            }
          ];

          //
          switch (Ntype) {
            case 1:
              try {
                this.editor.set(this.reqArr[newTpl].reqData);
              } catch (err) {
                this.$notify.error({
                  title: "",
                  message: err.toString()
                });
                return;
              }
              break;
            case 2:
              this.formData = this.reqArr[newTpl].reqData;
              break;
            case 3:
              this.raw = this.reqArr[newTpl].reqData;
              break;
            default:
              break;
          }

          //type
          this.form.type = Ntype;
        })
      },
      /**
       * 
       */
      reset(){
        this.$emit("hideDialog", {"listchange": false});
      },
      /**
       * form
       */
      deleteFormItem(index){
        if (this.formData.length === 1) {
          this.$message.warning({
            message: "Form"
          });
          return;
        }
        this.formData.splice(index, 1);
      },
      /**
       * form
       */
      addFromItem(){
        this.formData.push({key: "", vale: ""})
      },
      /**
       * Api
       */
      submitEditor(){
        //
        const type = this.form.type;
        var item;
        switch (type) {
          case 1:
            try {
              item = this.editor.get();
            } catch (err) {
              this.$notify.error({
                title: "",
                message: err.toString()
              });
              return;
            }
            break;
          case 2:
            item = this.formData;
            break;
          case 3:
            item = this.raw;
            break;
          default:
            break;
        }
        this.reqArr[this.form.template] = {
          type: this.form.type,
          reqData: item
        };

        //
        const formData = {
          reqArr: this.reqArr,
          id: this.dialog_id,
          templateOptions: this.templateOptions,
          ...this.form,
        };
        _addReqApi(formData).then((res) => {
          const data = res.data;
          if (data.code === 200) {
            this.$emit("hideDialog", {"listchange": true});
            this.$message.success({
              message: data.msg,
            });
          } else {
            this.$message.error({
              message: data.msg
            });
          }
        })
      },
      /**
       * 
       */
      init(){

        //JSONEditor
        this.editor.set({
          request: ""
        });

        //Form
        _.assign(this.form, {
          name: '',
          status: true,
          type: 1,
          template: "1"
        });

        //reqArr
        this.reqArr[this.form.template] = {
          type: 1,
          reqData: {
            request: "",
          }
        };

        //
        this.templateOptions = [{
          value: "1",
          label: ''
        }];
      }
    },
    mounted(){
      /**
       * 
       */
      this.$on('openDialog', function () {
        if (this.dialog_id === -1) {
          this.init();
          this.setWatch();
        } else {
          _getReqApi(this.dialog_id).then((res) => {
            const data = res.data, result = data.result;
            if (data.code === 200) {
              this.form = {
                name: result.name,
                status: result.status,
                type: result.type,
                template: result.template
              };
              this.templateOptions = result.templateOptions;
              this.reqArr = result.reqArr;
              const reqData = this.reqArr[this.form.template].reqData;
              switch (this.reqArr[this.form.template].type) {
                case 1:
                  this.editor.set(reqData);
                  break;
                case 2:
                  this.formData = reqData;
                  break;
                case 3:
                  this.raw = reqData;
                  break;
                default:
                  break;
              }
              this.setWatch();
            } else {
              this.$notify.error({
                title: '',
                message: data.msg
              });
            }
          });
        }
      });
      /**
       *
       */
      this.$on('closeDialog', function () {
        this.reqArr = {};
        this.unwatch();
      });
      /**
       * 
       */
      this.editor = new JSONEditor(this.$refs.json_editor, {
        onError: (err) => {
          this.$notify.error({
            title: "",
            message: err.toString()
          });
        }
      });
      this.editor.setMode('code');
    }
  }
</script>



